.pl-login-form {
  width: 360px;
  margin: 50px auto;
}

.pl-login-form form {
  margin-bottom: 15px;
  background: #f7f7f7;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  padding: 30px;
}

.pl-login-form h2 {
  margin: 0 0 15px;
}

.pl-login-form .UH_form .form-bottom {
  font-size: 16px;
}

.pl-login-form .UH_form .form-bottom p {
  margin: 0;
}